
import { defineComponent, computed } from 'vue'
import { formatDate } from '@/utils/format-date'
export default defineComponent({
  props: {
    title: {
      type: String,
      default: ''
    },
    listData: {
      type: Array,
      required: true
    },
    totalCount: {
      type: Number,
      default: 0
    },
    propList: {
      type: Array,
      required: true
    },
    isOperation: {
      type: Boolean,
      default: false
    },
    border: {
      type: Boolean,
      default: false
    },
    showIndexColumn: {
      type: Boolean,
      default: false
    },
    showSelectColumn: {
      type: Boolean,
      default: false
    },
    operation: {
      type: Object,
      required: true
    },
    page: {
      type: Object,
      default: () => ({ currentPage: 0, pageSize: 10 })
    },
    childrenProps: {
      type: Object,
      default: () => ({})
    },
    showFooter: {
      type: Boolean,
      default: true
    }
  },
  emits: ['selectionChange', 'update:page'],
  setup(props, { emit }) {
    const isShowOperationCols = computed(() => {
      let list = props.operation.cols
      return list.length && list.length > 0
    })
    const isShow = computed(() => {
      return props.isOperation && props.operation.cols.length
    })
    const filters = (row: any, options: any) => {
      let list = options.value.filter((e: any) => e.value === row)
      return list.length > 0 ? list[0].label : '--'
    }
    const timeFilters = (row: any) => {
      if (row) {
        return formatDate(row)
      }
      return '--'
    }
    const handleSelectionChange = (value: any) => {
      emit('selectionChange', value)
    }

    const handleCurrentChange = (currentPage: number) => {
      emit('update:page', { ...props.page, currentPage })
    }
    const handleSizeChange = (pageSize: number) => {
      emit('update:page', { ...props.page, pageSize })
    }

    return {
      handleSelectionChange,
      handleCurrentChange,
      handleSizeChange,
      isShowOperationCols,
      isShow,
      filters,
      timeFilters
    }
  }
})
